import Api from '../../api/wo';
export default {
	onLoad: function(option) {
		Api.getWOInfo(option.orderId).then(res => {
			console.log(res)
			this.orderId = option.orderId
			this.person = res.data.person
			this.phone = res.data.phone
			this.br = res.data.br
			this.fault = res.data.fault
			this.machine = res.data.machine
			this.sts = res.data.wosts
			const tmp = res.data.images.split(';')
			if (tmp[0] !== '') {
				const urls = []
				for (const index in tmp) {
					const url = tmp[index]
					urls.push(url)
				}
				this.images = urls;
			}
		})
		Api.initWO().then(res => {
				this.bkName = res.data.org
				console.log(res.data)
				this.machinePickerArray = res.data.machinePicker
				this.faultPickerArray = res.data.faultPicker
				this.multiArray[0] = res.data.machinePicker
				this.multiArray[1] = res.data.machinePicker[0].children
				this.machineId = this.multiArray[1][0].value
				this.orgId = res.data.orgId
				uni.hideLoading()
			})
	},
	data() {
		return {
			orderId: '',
			person: '',
			phone: '',
			fault: '',
			machine: '',
			br: '',
			sts: '',
			operationInfo: '',
			images: [],
			index: -1,
			imgList: [],
			bkName: '',
			multiArray: [],
			multiIndex: [0, 0],
			machinePickerArray: [],
			faultPickerArray: [],
			flag: false,
			machineId:'',
			faultId:'',
			description:'',
			orgId:''
		};
	},
	methods: {
		PickerChange(e) {
			if(e.detail.value === -1){
				this.index = 0	
			}else{
				this.index = e.detail.value
			}
			this.faultId = this.faultPickerArray[this.index].value
		},
		MultiChange(e) {
			this.multiIndex = e.detail.value
			if(this.multiArray[0][this.multiIndex[0]].value == '99999'){
				this.machineId = this.multiArray[0][this.multiIndex[0]].value
			}else{
				this.machineId = this.multiArray[1][this.multiIndex[1]].value
			}
		},
		MultiColumnChange(e) {
			let data = {
				multiArray: this.multiArray,
				multiIndex: this.multiIndex
			};
			let len = data.multiArray[0].length
			console.log('len:'+len)
			data.multiIndex[e.detail.column] = e.detail.value
			if(data.multiIndex[0] == 5){
				data.multiArray[1] = []
				this.multiIndex.splice(1, 0)
			}
			for(let i = 0 ; i < len ; i++){
				if(data.multiIndex[0] == i){
					data.multiArray[1] = data.multiArray[0][i].children
					this.multiIndex.splice(1, 0)
					break
				}
			}
			this.multiArray = data.multiArray
			this.multiIndex = data.multiIndex
		},
		setOperationInfo(e) {
			this.operationInfo = e.detail.value
		},
		onImageTouch(index) {
			uni.previewImage({
				current: index,
				urls: this.images
			})
		},
		upOperation() {
			let data = {
				orderId: this.orderId,
				person: this.person,
				operationInfo: this.operationInfo,
				machineId: this.machineId,
				faultId: this.faultId,
			}
			console.log(data)
			if (data.operationInfo == '') {
				uni.showToast({
					title: '请输入理由',
					icon: 'none'
				})
				return
			}
			if(data.faultId==''){
				uni.showToast({
					title: '请选择故障类型',
					icon: 'none'
				})
				return
			}
			uni.showLoading({
				title: '提交中',
			})
			Api.uploadOperation(data).then(res => {
				uni.hideLoading()
				if (res.code == 200) {
					uni.showModal({
						title: "提交成功",
						content: "你已完成提交！",
						showCancel: false,
						confirmText: "完成",
						success: function() {
							uni.hideLoading()
							uni.reLaunch({
								url: '/pages/engineer/engineer'
							})
						}
					})
				} else {
					wx.showToast({
						icon: 'none',
						title: '提交失败',
						mask: true,
						duration: 2000
					})
				}
			})
		},
		acceptNowWO(orderId) {
			uni.showLoading({
				title: '接受中',
			})
			Api.acceptWO(orderId).then(res => {
				uni.hideLoading()
				uni.showToast({
					icon: 'success',
					mask: true,
					title: '接受工单成功',
					duration: 1000
				})
				setTimeout(function() {
					uni.reLaunch({
						url: '/pages/engineer/engineer'
					})
				}, 1000)
			})
		},
		goWODetail(orderId) {
			uni.navigateTo({
				url: '/pages/operation_his/operation_his?orderId=' + orderId
			})
		},
		intoChangePerson(orderId) {
			uni.navigateTo({
				url: '/pages/query_nextperson/query_nextperson?orderId=' + orderId
			})
		},
	}
}
